.movie-genre-layout {
	position: relative;
	.movie-genere-header {
		position: sticky;
		top: 0;
		right: 0;
		left: 0;
		width: 100%;
		padding: 0 1.4rem 0 1.4rem;
		box-sizing: border-box;
		.movie-genere-nav {
			z-index: 2;
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 6rem;
			.genere-nav-header {
				display: flex;
				align-items: center;
				gap: 1rem;
				flex: 1;
				input {
					width: 80%;
					border: none;
					outline: none;
					height: 2.5rem;
					color: white;
					background-color: black;
					padding-left: 1rem;
				}
				h1 {
					font-size: 1.7rem;
					font-weight: 400;
				}
			}
			.genere-nav-icon {
				width: 1.5rem;
				height: 1.5rem;
				cursor: pointer;
			}
			.genere-nav-img {
				position: absolute;
				z-index: -1;
				width: 100%;
				top: 0;
				left: 0;
				right: 0;
				height: 9rem;
			}
		}
	}
	main {
		padding: 2rem 1.4rem 0 1.4rem;
	}
}
